.LoginForm{
    width: 100%;
}

.SpinnerContainer{
    width: 100%;
    text-align: center;
}

.Title{
    text-align: center;
}

.ButtonGroup{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

Button{
    display: block;
    margin-top: 5px;
    width: 95%;
}

.LoginButton{
    height: 2rem;
    border: 0.4px solid #d2d3c9;
    border-radius: 8px;
    font-size: larger;
    font-weight: 400;
    background-color: white;
    transition: linear 0.2s;
}

.LoginButton:hover{
    border: none;
    background-color: #a8df65;
    transform: scale(1.02);
    color: white;

}

.RegisterButton{
    height: 2rem;
    border: 0.4px solid #d2d3c9;
    border-radius: 8px;
    font-size: larger;
    font-weight: 400;
    background-color: white;
    transition: ease-in-out 0.2s;
}

.RegisterButton:hover{
    color: #ee91bc;
    background-color: #edf492;
    transform: scale(1.02);
}